import {Box, Image} from "@mantine/core";
import React from "react";
import {useTheme} from "@emotion/react";
import {useMediaQuery} from "@mantine/hooks";
import GroupBanner from "./components/GroupBanner/GroupBanner";
import npc from './static/npc.webp';
import Land3d from './static/minecraftLand3d.webp';
import HeroHeader from "../../components/HeroHeader";
import InformationSection from "./components/InformationSection";
import ModulesSection from "./components/ModulesSection";


const groupBannerTitle = ['Bienvenidos al Mundo de la Programacin', 
                        'Gamificacin'];
const groupBannerDescription= ['Nuestro curso de Introduccin al Mundo de la Programacin tiene como objetivo despertar el pensamiento crtico en los nios desde temprana edad. Se impartirn conceptos como variables, tipo de datos, bucles, sentencias condicionales, etc. Las lecciones sern complementadas con actividades prcticas de programacin dentro de Minecraft.',
                              'La metodologa de nuestro curso para nios es que los chicos aprendan mientras se divierten en su videojuego favorito, en este caso Minecraft. La gamificacin presente alrededor de la curva de aprendizaje ser la clave para desarrollar el pensamiento lgico del estudiante. Anmate!'];
const stackBannerTitle = ['Aprende Jugando'];
const stackBannerDescription = ['Construye la ciudad de tus sueos mientras desarrollas tus habilidades como programador.'];
const themeMantineColor = ['teal', '7',];

function CursoInfantil() {

    const theme = useTheme();
    const isLargeScreen = useMediaQuery(`(min-width: ${theme.breakpoints.sm} )`);
    const xPadding = isLargeScreen ? 128 : "xs";

    return <>
        <HeroHeader color={"teal"} title={"!Diviertete Aprendiendo con Minecraft!"}
                    rightSection={<Image src={Land3d}/>} barColorIndex={themeMantineColor[1]}/>
        <Box px={xPadding}>

            <InformationSection groupBannerTitle={groupBannerTitle[0]} groupBannerDescription={groupBannerDescription[0]} 
            stackBannerTitle={stackBannerTitle[0]} stackBannerDescription={stackBannerDescription[0]}/>
            <ModulesSection/>

            <GroupBanner
                img={npc}
                title={groupBannerTitle[1]}
                description={groupBannerDescription[1]}
                imgWidth={isLargeScreen ? 358.5 : 150}
                bottomSection={<Box/>}
            />
        </Box>
    </>

}

export default CursoInfantil;